.navbar{
    width: 100%;
    border-radius: 0;
    margin-bottom: 0;
}
.navbar-inverse{
    position:sticky;
    top: 0;
    z-index: 2;
}
li:active{
   
   font-weight: bolder;
   color: white;

}

ul{
    color:white;
}
.navbar-inverse a{
    color:white;
    font-size: 21px;
    margin: 0;
}

.float-right{
    float: right;
}
.container-fluid>.navbar-header{
    padding: 0;
    margin: 0;
}

.navbar-brand{
   margin-left: 0px;
}

.navbar>.container-fluid .navbar-brand{
    margin-left: 0;
}