.rotated{
    border: 10px solid #fff;
   transform:rotateZ(-25deg);
    margin-top: 150px;
    margin-left: 150px;
    box-shadow: 20px 20px 20px 20px  gray;
}
.rotate-container{
    width: 500px;
    height: 500px;
}

.container-fluid{
    padding: 0;
}

.header-h{
    background-color: cadetblue;
    max-height: fit-content;
    overflow: hidden;
}

.straight{
    background-color: rgba(255, 255, 255, 0.637);
    background-size: inherit;
    position: absolute;
    left: 40%;
    top: 40%;
}

@media screen and (max-width:480px) {
    .straight{
        left: 0%;
        top: 5%;
        font-size: 20px;
    }
    .straight h1{
        font-size: 18px;

    }
    .straight h3{
        font-size: 14px;
    }

   

}

@media screen and (max-width:1024px) {
    .straight{
        left: 20%;
        top: 10%;
        
    }
}
@media screen and (max-width:1024px){
    .straight{
        left: 20%;
        top:20%
    }
    .straight h1{
        font-size: 20px;
    }
    .straight h3{
        font-size: 16px;
    }
    .straight h2{
        font-size: 18px;
    }
}